.header .header-top {
  height: 50px;
  line-height: 50px;
  padding: 0 20px;
  text-align: right;
  background-color: #e0e6ec;
}
.header .header-top .logo {
  line-height: 60px;
  text-align: left;
  font-size: 18px;
}
.header .header-top .logo img {
  height: 40px;
  vertical-align: middle;
}
.header .header-top .logo span {
  margin-left: 5px;
}
.header .header-top a {
  margin-left: 40px;
}
