/** 常用色值 **/
/** 常用字体大小 **/
.footer {
  height: 100px;
  padding: 40px 0;
  text-align: center;
  color: #ffffff;
}

/** 常用色值 **/
/** 常用字体大小 **/
.login-page .footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 60px;
  padding-top: 20px;
  background: rgba(21, 23, 20, 0.5);
  font-size: 12px;
  letter-spacing: 0.15em;
}
.login-page .ant-input-lg,
.login-page .ant-btn-lg {
  height: 40px;
  font-size: 16px;
}
.login-page .login-form-button {
  width: 80%;
  margin-top: 15px;
  margin-left: 6%;
}
.login-header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 80px;
  line-height: 80px;
  background: rgba(21, 20, 13, 0.9);
}
.login-header .logo {
  padding-left: 100px;
  font-size: 20px;
  color: white;
}
.login-header img {
  height: 40px;
  margin-right: 15px;
}
.login-content-wrap {
  min-height: 550px;
  height: calc(100vh);
  background: url("/assets/back.jpg") no-repeat center;
  background-size: cover;
}
.login-content {
  position: relative;
  top: 50%;
  display: -webkit-flex;
  display: flex;
  max-width: 960px;
  margin: 0 auto;
  padding: 0 20px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  margin-left: 10;
}
.login-content .word {
  -webkit-flex: 7 1;
          flex: 7 1;
  line-height: 1.4;
  color: #000000;
  font-size: 64px;
  font-weight: bold;
}
.login-box {
  position: relative;
  -webkit-flex: 4 1;
          flex: 4 1;
  height: 370px;
  padding: 40px 30px 0 30px;
  background: rgba(112, 128, 144, 0.5);
  overflow: hidden;
}
.login-box .title {
  line-height: 1;
  margin-bottom: 50px;
  font-size: 40px;
  color: #333;
  font-weight: bold;
  text-align: left;
}
.login-box .error-msg-wrap {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
.login-box .error-msg-wrap div {
  visibility: hidden;
  height: 30px;
  line-height: 30px;
  background: #f60c1a;
  color: #ffffff;
  font-size: 16px;
  text-align: center;
  -webkit-transform: translateY(-30px);
          transform: translateY(-30px);
  transition: all 0.3s ease-out;
}
.login-box .error-msg-wrap div.show {
  visibility: visible;
  -webkit-transform: translateY(0);
          transform: translateY(0);
  transition: all 0.3s ease-out;
}
@media screen and (max-width: 1200px) {
  .login-header {
    padding-left: 30px;
    padding-right: 30px;
  }
  .login-content .word {
    font-size: 48px;
  }
}

.header .header-top {
  height: 50px;
  line-height: 50px;
  padding: 0 20px;
  text-align: right;
  background-color: #e0e6ec;
}
.header .header-top .logo {
  line-height: 60px;
  text-align: left;
  font-size: 18px;
}
.header .header-top .logo img {
  height: 40px;
  vertical-align: middle;
}
.header .header-top .logo span {
  margin-left: 5px;
}
.header .header-top a {
  margin-left: 40px;
}

.nav-left .logo {
  line-height: 90px;
  padding-left: 20px;
  background-color: #002140;
}
.nav-left .logo img {
  height: 35px;
}
.nav-left .logo h1 {
  color: #ffffff;
  font-size: 25px;
  display: inline-block;
  vertical-align: middle;
  margin: 0 0 0 20px;
}

.container .nav-left {
  height: calc(100vh);
  background-color: #001529;
}
.container .main {
  -webkit-flex: 1 1;
          flex: 1 1;
  height: calc(100vh);
}
.container .content {
  position: relative;
  padding: 20px;
}

.home-wrap {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-content: center;
          align-content: center;
  height: calc(100vh);
}

